.slider{
  display: flex;
  justify-content: space-between;
  width: 100%;
}
.slide{
  width: calc(416 / 1312 * 100%);
  color: white;
  position: relative;
  &:hover{
    .slide__button{
      opacity: 1;
    }
  }
  &__img{
    display: block;
    width: 100%;
    height: 312px;
    object-fit: cover;
    margin-bottom: 16px;
  }
  &__date{
    font-family: $font-light;
    font-size: 12px;
    @include lineAndSpacing(100%);
    text-transform: uppercase;
    padding-bottom: 16px;
    border-bottom: 1px solid rgba(255,255,255,.2);
    margin-bottom: 16px;
  }
  
  &__title{
    font-family: $font-regular;
    font-size: 24px;
    @include lineAndSpacing(120%);
  }
  &__button{
    position: absolute;
    left: -5%;
    top: -5%;
    opacity: 0;
    transition: 0.2s;
    cursor: pointer;
  }
}
