.first-screen{
  position: relative;
  overflow: hidden;
  width: 100vw;
  height: 100vh;
  background-image: linear-gradient(0deg, rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.6));
  padding: 64px;
  display: flex;
  align-items: flex-end;
}
.first-screen__image{
  position: absolute;
  width: 100%;
  top: 0;
  left: 0;
  z-index: -1;
}

.first-screen__inner{
  color: white;
}
.first-screen__label{
  margin-bottom: 16px;
}
.first-screen__title{
  margin-bottom: 180px;
  h2{
    font-family: $font-medium;
    font-weight: 500;
    font-size: 56px;
  }
}
.first-screen__description{
  max-width: 360px;
  width: 100%;
  opacity: 0.6;
}