}
a{
  text-decoration: none;
}

.scroll-line{
  display: block;
  //position: absolute;
  padding-right: 8px;
  padding-left: 8px;
  height: 64px;
  cursor: pointer;  
  &__inner{
    display: block;
    width: 1px;
    height: 100%;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    background-color: rgba(255,255,255,0.3);
    &:after{
      content: '';
      position: absolute;
      left: 0;
      width: 1px;
      height: 44px;
      background-color: rgba(255,255,255,1);
    }
  }
  &_top{
    &:after{
      top: 0;
    }
  }
}


.logo_tftl{
  display: block;
  margin-left: auto;
  align-self: flex-end;
}