.section_blockquote{
  padding: 255px 0 262px;
}
.blockquote{
  width: 100%;  
  max-width: 528px;
  margin: 0 auto;
  position: relative;
  &:before{
    position: absolute;
    left: 50%;
    top: 0;
    transform: translate(-50%, -150%) rotate(180deg);
    content: '"';
    display: block;
    font-size: 32px;
    font-family: $font-light;
    line-height: 110%;
  }
}
.blockquote__text{
  font-family: $font-light;
  font-size: 32px;
  text-align: center;
  color: black;
  @include lineAndSpacing(110%);
}
.blockquote__author{
  margin-top: 32px;
  text-align: center;
}