//  Section main

.section_main{
  width: 100vw;
  height: 100vh;
  position: relative;
  padding: 32px 0 40px;
}
.section__scroll-line_main{
  position: absolute;  
  bottom: 0;    
  right: calc(64 / 1440 * 100%);
  z-index: 5;
}

.swiper-slider__container{
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}
.swiper-container_main{
  width: 100%;
  height: 100%;
}
.swiper-slide{
  background-position: center 20%;
  background-size: cover;
  overflow: hidden;    
  &__inner{
    background-image:linear-gradient(0deg, rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.6));
    background-position: center;
    position: relative;
    width: 100vw;
    height: 100vh;   
    &:hover{      
      cursor: url(../images/cursor.png), auto;
    }
   
  }
