  //   img{
  //     width: 100%;
  //     left: 0;
  //     top: 50%;
  //     transform: translateY(-20%);
  //     display: block;
  //     z-index: -1;
  //   }
  //   &:after{
  //     content: '';
  //     position: absolute;
  //     display: block;
  //     left: 0;
  //     top: 0;
  //     right: 0;
  //     bottom: 0;
  //     background-image:linear-gradient(0deg, rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.6));
  //   }
  // }
  &__title{
    font-family: $font-medium;
    font-size: 72px;
    font-weight: 500;
    @include lineAndSpacing(100%);
    color: white;
    display: block;
    position: absolute;
    left: 4.72%;
    top: 28.63%;
    max-width: 932px;
    pointer-events: none;
  }
  &__info{
    position: absolute;
    top: 56.58vh;    
    left: 41.81%;
