    flex-direction: row;
    pointer-events: none;
  }
  &__label{
    min-width: 46px;
    margin-right: 32px;
    pointer-events: none;
  }
  &__text{
    font-family: $font-light;
    font-size: 14px;
    @include lineAndSpacing(160%);
    max-width: 248px;
    pointer-events: none;
  }
  &__button{
    position: absolute;
    top: calc(324 / 744 * 100%);
    left: calc(1082 / 1440 * 100%);
  }
}
.wrapper_section_main{
  height: 100%;
  position: relative;
