//  Section about

.section_about{
  padding: 156px 0;
}

//  Section services

.services__container{
  width: 100%;  
  display: flex;
  justify-content: space-between;
  margin-bottom: -130px;
  position: relative;
  z-index: 1;
}
.service{
   width: calc(416 / 1312 * 100%);   
   position: relative;
   &:after{
    content: '';
