//  Section comunity

.section_comunity{
  padding-bottom: 72px;
  padding-top: 156px;
  position: relative;
  z-index: 2;
}
.section__slider_comunity{
  margin-top: 104px;
}

//  Section technologies
.section_tech{  
  padding-top: 286px;
}
.section__techs{
  margin-top: 104px;
  position: relative;
  z-index: 2;
}

.techs__list{
  position: relative;
  z-index: 2;
}
.tech{
  &__item{
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    color: white;
    padding: 40px 0;
    border-top: 1px solid rgba(255,255,255,.2);
    transition: 0.2s;
    cursor: pointer;
    &:last-child{
      border-bottom: 1px solid rgba(255,255,255,.2);
      
    }
    &:hover{
      .tech__title, .tech__label{
        opacity:1;
      }
      .tech__desc{
        opacity: 0.6;
      }
    }
  }
  &__label{
    align-self: flex-start;
    opacity: 0.6;
    transition: 0.2s;    
    margin-right: 16px;
  }
  &__title{
    font-family: $font-medium;
    font-size: 56px;
    opacity: 0.6;
    transition: 0.2s;
    line-height: 50px;
    margin-left: 16px;
  }
  &__desc{
    font-family: $font-light;
    font-size: 12px;
    opacity: 0.6;
    max-width: 304px;
    margin-left: auto;
    @include lineAndSpacing(160%);
    opacity: 0;
    transition: 0.2s;
  }
  
  &__images{
    position: absolute;
    width: 350px;
    height: 254px;
    transform: translateY(-50%) rotate(6deg);
    overflow: hidden;
    top: 50%;
    left: 43.125%;
    z-index: 1;
  }
  &__image{
    display: block;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    height: 100%;
    opacity: 0;
    transition: 0.2s;  
    object-fit: cover;
    width: 100%;
  }
  &__image_visible{
    opacity: 1;
  }
}



// Section map
.section_map{
  position: relative;
  width: 100%;
  &:after{
    content: '';
    display: block;
    width: 100%;
    padding-top: 1168px;
    pointer-events: none;
  }
}
.map{
  bottom: 0;
  left: 0;
  position: absolute;
  width: 100%;
  z-index:1;  
}
.map__info{
  position: absolute;
  width: 100%;
  height: 100%;
  top:0;
  left: 0;
  padding-top: 156px;
  pointer-events: none;
  z-index: 2;
  pointer-events: none;
}
