.button_circle{
  width: 212px;
  height: 212px;
  border-radius: 50%;
  border: 1px solid rgba(255,255,255,0.3);
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  pointer-events: all;
  cursor: pointer;
}
.button_circle_right{
  &:after{
    content:'';
    background-image:url('../images/arrow_right_white.png');
    background-position: center;
    background-size: contain;
    display: block;
    width: 68px;
    height: 11px;
    position: absolute;
    left: 75%;
    top: 50%;
    transform: translateY(-50%);
  }
}